import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { Icon, Menu } from "semantic-ui-react";
import { authService } from "../Instance";

const Navigation = () => {
  const [activeItem, setActiveItem] = useState("Home");

  const history = useHistory();

  const onLogOutClick = () => {
    authService.signOut();
    history.push("/RollingPaper");
  };

  const onItemClick = (e, { name }) => setActiveItem({ name }.name);

  return (
    <Menu size="large" widths="3" color="yellow" fixed="top">
      <Menu.Item
        as={Link}
        to="/RollingPaper"
        name="Home"
        active={activeItem === "Home"}
        onClick={onItemClick}
      />
      <Menu.Item
        as={Link}
        to="/RollingPaper/RollSheet"
        name="RollSheet"
        active={activeItem === "RollSheet"}
        onClick={onItemClick}
      />

      <Menu.Item onClick={onLogOutClick}>
        <Icon name="log out" size="large" />
      </Menu.Item>
    </Menu>
  );
};

export default Navigation;
